<template>
    <div id="swe-dashboard">
        <h3>
            Time In and Time out of SWE
        </h3>
        <div class="time-in-out">
            <button @click="timeIn" class="btn black">Time-in</button>
            <button  @click="timeOut" class="btn black">Time-out</button>
        </div>
        <div v-if="!timedIn"> </div>
        <div v-else>
            You have timed in : {{timein}}
        </div>
        <div v-if="!timedOut"> </div>
        <div v-else>
            You have timed out : {{timeout}}
        </div>

    </div>
</template>

<script>
import firebase from 'firebase'
import db from '../firebase'

export default {
    name: 'swe-dashbaord',
    data(){
        return {
            timedIn: false,
            timein: '',
            timedOut: false,
            timeout: '',
            timeref: ''
        }
    },
    computed:{
        totalHours(){
            let x = 10
            let y = this.timeout
            let testhrs = Math.abs(y) + 20

            const totaltime = testhrs - x

            console.log(totaltime)
        }
    },
    methods: {
        timeIn(e){
        // db.collection('Timsheet').add({
        //     Name: '',
        //     Timein: new Date(),
        //     Timeout: '',
        //     NightDiff: '',
        //     TotalHours: '',
        //     Overtime: '',
        //     WFH: '',
        //     Remarks: ''
        // })
        // .then(docRef => {
        //     console.log("I have written this document to firebase", docRef.id)
        // })
        // .catch(err => {
        //     console.log("Error adding a document", err)
        // })
            
            db.collection('Timsheet').add({
                Name: firebase.auth().currentUser.email,
                Timein: new Date(),
                Timeout: '',
                NightDiff: '',
                TotalHours: '',
                Overtime: '',
                Remarks: '',
            }).then(docRef => {
                this.timedIn = true
                this.timein = new Date().toLocaleString()
                this.timeref = docRef.id
                console.log(this.timeref)
                alert('you have successfully timed in')
            })
            // console.log(firebase.auth().currentUser.email)
            e.preventDefault()
        },
        timeOut(e){
            
            db.collection('Timsheet').doc(this.timeref).update({
                Timeout: new Date()
            }).then(()=>{
                this.timeout = new Date().toLocaleString()
                alert('you have successfully timed out')
            }).catch(function(error){
                console.log("Error in updating", error)
            })
            
            e.preventDefault()
        }
    }
}
</script>

<style>

</style>



