// import axios from 'axios'
import db from '../../firebase'

const state = {
    timesheet: []
}

const getters = {
    allData: (state) => state.timesheet
}

const actions = {
    retrieveData(context){
        db.collection('Timsheet').get()
        .then(querySnapshot => {
            let tempData = []
            querySnapshot.forEach(doc =>{
                const timestamp1 = doc.data().Timein
                const timestamp = doc.data().Timeout
                let timeinDate = new Date(timestamp1.seconds * 1000).toLocaleDateString()
                let timeinHrs = new Date(timestamp1.seconds * 1000).toLocaleTimeString()
                let timeoutDate = new Date(timestamp.seconds * 1000).toLocaleDateString()
                let timeoutHrs  = new Date(timestamp.seconds * 1000).toLocaleTimeString()
                
                const data = {
                    id: doc.id,
                    Name: doc.data().Name,
                    Timein: timeinDate + ' ' + timeinHrs,
                    Timeout: timeoutDate + ' ' + timeoutHrs,
                    NightDiff: doc.data().NigthDiff, 
                    TotalHours: doc.data().TotalHours,
                    Overtime: doc.data().Overtime,
                    WFH: doc.data().WFH,
                    Remarks: doc.data().Remarks
                }
                tempData.push(data)
            })
            context.commit('retrieveData', tempData)
        })
 
    },

    // async retrieveData({ commit }) {
    //     const response = await axios.get('')

    //     console.log(response.data)
    // }

    logTime(){
        // db.collection('Timsheet').add({
        //     Name: 'This is a test user',
        //     Timein: new Date(),
        //     Timeout: '',
        //     NightDiff: '',
        //     TotalHours: '',
        //     Overtime: '',
        //     WFH: '',
        //     Remarks: ''
        // })
        // .then(docRef => {
        //     console.log("I have written this document to firebase", docRef.id)
        // })
        // .catch(err => {
        //     console.log("Error adding a document", err)
        // })
    }
}

const mutations = {
    retrieveData(state, timesheet){
        state.timesheet = timesheet
    },
    logTime(state, timesheet){
        state.timesheet = timesheet
    }
}


export default{
    state,
    getters,
    actions,
    mutations
};


